@import '~@wp-plugins/adapters/src/theme/variables';

@mixin botField($tokenLength, $usrLength, $updateLength) {
	.bot_token {
		flex: 0 0 $tokenLength;
	}
	.bot_username {
		flex: 0 0 $usrLength;
	}
	.update_method {
		flex: 0 0 $updateLength;
	}
}

.field-group-items {
	&.bot-group {
		display: flex;
		flex-flow: row wrap;
		@include botField(50%, 25%, 25%);
		@media screen and (max-width: $breakpointSm) {
			@include botField(100%, 100%, 100%);
		}
		@media screen and (min-width: $breakpointSm) and (max-width: $breakpointMd) {
			@include botField(100%, 50%, 50%);
		}
		@media screen and (min-width: $breakpointMd) and (max-width: $breakpointLg) {
			@include botField(70%, 30%, 100%);
		}
		@media screen and (min-width: $breakpointLg) and (max-width: $breakpointXl) {
			@include botField(100%, 50%, 50%);
		}
		@media screen and (min-width: $breakpointXl) {
			@include botField(50%, 30%, 20%);
		}
	}
}
